import { Box } from "@mui/material";

import CollateralBalance from "./components/CollateralBalance";
import CollateralBoard from "./components/CollateralBoard";

function Home() {
	return (		
		<Box sx={{backgroundColor: 'rgba(9, 9, 10, 1)', display: 'flex', height: 'calc(100vh - 187px)', justifyContent: 'center', pt: 3, px: 1}}>	
			<Box sx={{maxWidth: '1124px', width: '100%'}}>	
      	<CollateralBalance />
				<CollateralBoard />
			</Box>
		</Box>		
	)
}

export default Home;