import { useState, useEffect } from "react";

import { useSelector } from "react-redux";
import { IReduxState } from "../../../store/slices/state.interface";
import {
  calcUserAccountDetails,
  changeApproval,
  changeDepositCollateral,
  withdrawCollateral,
} from "../../../store/slices/user-slice";
import { useDispatch } from "react-redux";
import { Trade } from "../../../container/trade";
import { AppDispatch } from "../../../store/store";

import Big from "big.js";
import { useWeb3Context } from "../../../hooks/web3Context";

import {
  Box,
  Button,
  FormControl,
  IconButton,
  InputAdornment,
  Modal,
  OutlinedInput,
  Table,
  TableContainer,
  TableBody,
  TableRow,
  TableCell,
  TableHead,
  Paper,
  Typography,
} from "@mui/material";
import { makeStyles } from '@mui/styles';

import CloseIcon from "@mui/icons-material/Close";

const modalStyle = {
  position: "absolute" as "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  maxWidth: "28rem",
  bgcolor: "background.paper",
  background: "rgb(38, 40, 42)",
  borderRadius: "24px",
  outline: "transparent solid 2px",
  outlineOffset: "2px",
  boxShadow:
    "rgba(0, 0, 0, 0.1) 0px 0px 0px 1px,rgba(0, 0, 0, 0.2) 0px 5px 10px,rgba(0, 0, 0, 0.4) 0px 15px 40px",
  p: 2,
};

const TokenList = ["USDC", "WETH", "FRAX", "OP", "USDT", "ETH"];

function CollateralBoard() {
  const dispatch = useDispatch<AppDispatch>();
  const { address, provider, chainID, connected } = useWeb3Context();
  const { perpetualObject } = Trade.useContainer();
  const [openDepositModal, setDepositModalOpen] = useState(false);
  const [openWithdrawModal, setWithdrawModalOpen] = useState(false);
  const [modalIndex, setModalIndex] = useState(0);

  const [tokenValue, setTokenValue] = useState<number>(0);

  const isUserAccountCalculated = useSelector<IReduxState, boolean>((state) => {
    return state.user.isUserAccountCalculated;
  });

  const collateralBalanceList = useSelector<IReduxState, Big[]>((state) => {
    return state.user.collateralBalanceList;
  });

  const collateralFreeCollateralList = useSelector<IReduxState, Big[]>(
    (state) => {
      return state.user.collateralFreeCollateralList;
    }
  );

  const allowanceList = useSelector<IReduxState, Big[]>((state) => {
    return state.user.allowanceList;
  });

  const collateralTokenPriceList = useSelector<IReduxState, number[]>(
    (state) => {
      return state.user.collateralTokenPriceList;
    }
  );

  const balanceList = useSelector<IReduxState, Big[]>((state) => {
    return state.user.balanceList;
  });

  const balanceEth = useSelector<IReduxState, string>((state) => {
    return state.user.balanceEth;
  });

  const handleDepositModalOpen = (index: number) => {
    setDepositModalOpen(true);
    setModalIndex(index);
  };

  const handleWithdrawModalOpen = (index: number) => {
    setWithdrawModalOpen(true);
    setModalIndex(index);
  };

  const handleModalClose = () => {
    setDepositModalOpen(false);
    setWithdrawModalOpen(false);
    setTokenValue(0);
  };

  const handleTokenValueChange = (e: any) => {
    const floatRegExp = new RegExp("([0-9]+([.][0-9]*)?|[.][0-9]+)$");
    const dotRegExp = new RegExp("^([0-9]+[.][0]*)$");
    if (e.target.value === "" || floatRegExp.test(e.target.value)) {
      let filteredValue = e.target.value;
      if (dotRegExp.test(e.target.value)) {
        setTokenValue(filteredValue);
      } else {
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        filteredValue = Math.floor(filteredValue * 1000) / 1000;
        if (openDepositModal) {
          if (
            (modalIndex < 5 &&
              filteredValue <= Number(balanceList[modalIndex].toFixed(3))) ||
            (modalIndex === 5 && filteredValue <= Number(balanceEth))
          ) {
            setTokenValue(filteredValue);
          } else {
            if (modalIndex < 5) {
              setTokenValue(Number(balanceList[modalIndex].toFixed(2)));
            } else {
              setTokenValue(Number(balanceEth));
            }
          }
        } else {
          if (
            (modalIndex < 5 &&
              filteredValue <=
                Number(collateralFreeCollateralList[modalIndex].toFixed(3))) ||
            (modalIndex === 5 &&
              filteredValue <=
                Number(collateralFreeCollateralList[1].toFixed(3)))
          ) {
            setTokenValue(filteredValue);
          } else {
            if (modalIndex < 5) {
              setTokenValue(
                Number(collateralFreeCollateralList[modalIndex].toFixed(3))
              );
            } else {
              setTokenValue(Number(collateralFreeCollateralList[1].toFixed(3)));
            }
          }
        }
      }
    }
  };

  const handleMaxValue = () => {
    if (openDepositModal) {
      if (modalIndex < 5) {
        setTokenValue(Number(balanceList[modalIndex].toFixed(3)));
      } else {
        setTokenValue(Number(balanceEth));
      }
    } else {
      if (modalIndex < 5) {
        setTokenValue(
          Number(collateralFreeCollateralList[modalIndex].toFixed(3))
        );
      } else {
        setTokenValue(Number(collateralFreeCollateralList[1].toFixed(3)));
      }
    }
  };

  const CalcUserAccountDetails = function () {
    if (connected === true) {
      dispatch(
        calcUserAccountDetails({
          networkID: chainID,
          provider: provider,
          perpetualObject: perpetualObject,
        })
      );
    }
  };

  const onSeekApproval = async () => {
    await dispatch(
      changeApproval({
        provider: provider,
        networkID: chainID,
        tokenIndex: modalIndex,
        perpetualObject: perpetualObject,
      })
    );
  };

  const onSeekDeposit = async () => {
    if (modalIndex === 5) {
      await dispatch(
        changeDepositCollateral({
          address: address,
          provider: provider,
          networkID: chainID,
          amount: new Big(tokenValue),
          perpetualObject: perpetualObject,
        })
      );
    } else {
      await dispatch(
        changeDepositCollateral({
          address: address,
          provider: provider,
          networkID: chainID,
          tokenIndex: modalIndex,
          amount: new Big(tokenValue),
          perpetualObject: perpetualObject,
        })
      );
    }
  };

  const onSeekWithdraw = async () => {
    if (modalIndex === 5) {
      await dispatch(
        withdrawCollateral({
          address: address,
          provider: provider,
          networkID: chainID,
          amount: new Big(tokenValue),
          perpetualObject: perpetualObject,
        })
      );
    } else {
      await dispatch(
        withdrawCollateral({
          address: address,
          provider: provider,
          networkID: chainID,
          tokenIndex: modalIndex,
          amount: new Big(tokenValue),
          perpetualObject: perpetualObject,
        })
      );
    }
  };

  useEffect(() => {
    CalcUserAccountDetails();
  }, [connected]);

  return (
    <Box sx={{}}>
      <TableContainer
        component={Paper}
        sx={{
          mt: 2,
          borderRadius: "10px",
          backgroundColor: "rgba(29, 29, 29, 1)",
        }}
      >
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell
                sx={{
                  fontSize: "18px",
                  fontFamily: "Montserrat Medium",
                  borderBottom: "1px solid rgba(67, 70, 74, 1)",
                  color: "rgba(145, 153, 167, 1)",
                }}
              >
                COIN
              </TableCell>
              <TableCell
                sx={{
                  fontSize: "18px",
                  fontFamily: "Montserrat Medium",
                  borderBottom: "1px solid rgba(67, 70, 74, 1)",
                  color: "rgba(145, 153, 167, 1)",
                }}
                align="right"
              >
                WALLET
              </TableCell>
              <TableCell
                sx={{
                  fontSize: "18px",
                  fontFamily: "Montserrat Medium",
                  borderBottom: "1px solid rgba(67, 70, 74, 1)",
                  color: "rgba(145, 153, 167, 1)",
                }}
                align="right"
              >
                AMOUNT
              </TableCell>
              <TableCell
                sx={{
                  fontSize: "18px",
                  fontFamily: "Montserrat Medium",
                  borderBottom: "1px solid rgba(67, 70, 74, 1)",
                  color: "rgba(145, 153, 167, 1)",
                }}
                align="right"
              >
                WITHDRAWABLE
              </TableCell>
              <TableCell
                sx={{
                  fontSize: "18px",
                  fontFamily: "Montserrat Medium",
                  borderBottom: "1px solid rgba(67, 70, 74, 1)",
                  color: "rgba(145, 153, 167, 1)",
                }}
                align="right"
              >
                ACTION
              </TableCell>
            </TableRow>
          </TableHead>
          {isUserAccountCalculated && (
            <TableBody>
              <TableRow>
                <TableCell
                  component="th"
                  scope="row"
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                >
                  <Box>
                    <Typography
                      sx={{
                        fontSize: "22px",
                        lineHeight: "1.2",
                        fontFamily: "Montserrat Bold",
                      }}
                    >
                      USDC
                    </Typography>
                    <Typography
                      sx={{
                        fontSize: "18px",
                        lineHeight: "1.2",
                        fontFamily: "Montserrat",
                        color: "rgba(145, 153, 167, 1)",
                      }}
                    >
                      ${collateralTokenPriceList[0].toFixed(2)}
                    </Typography>
                  </Box>
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {balanceList[0].toFixed(2)} USD
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {collateralBalanceList[0].toFixed(2)}
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {collateralFreeCollateralList[0].toFixed(2)}
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  <Box sx={{ display: "flex", justifyContent: "flex-end" }}>
                    <Button
                      disabled={!connected}
                      sx={{
                        padding: "6px 16px",
                        borderRadius: "6px",
                        color: "rgb(238, 233, 226)",
                        textTransform: "none",
                        backgroundColor: "transparent",
                        boxSizing: "border-box",
                        border: "1px solid rgb(238, 233, 226)",
                        fontFamily: "Montserrat SemiBold",
                        "&: hover": {
                          backgroundColor: "rgb(234, 216, 184)",
                          color: "rgb(44, 46, 55)",
                        },
                        "&: disabled": {
                          backgroundColor: "transparent",
                          color: "rgb(238, 233, 226)",
                        },
                      }}
                      onClick={() => handleWithdrawModalOpen(0)}
                    >
                      Withdraw
                    </Button>
                    <Button
                      disabled={!connected}
                      sx={{
                        padding: "6px 16px",
                        borderRadius: "6px",
                        ml: 1,
                        color: "rgb(44, 46, 55)",
                        textTransform: "none",
                        backgroundColor: "rgb(248, 204, 130)",
                        fontFamily: "Montserrat SemiBold",
                        "&: hover": {
                          backgroundColor: "rgb(234, 216, 184)",
                          color: "rgb(44, 46, 55)",
                        },
                        "&: disabled": {
                          backgroundColor: "rgb(248, 204, 130)",
                          color: "rgb(44, 46, 55)",
                        },
                      }}
                      onClick={() => handleDepositModalOpen(0)}
                    >
                      Deposit
                    </Button>
                  </Box>
                </TableCell>
              </TableRow>
              <TableRow>
                <TableCell
                  component="th"
                  scope="row"
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                >
                  <Box>
                    <Typography
                      sx={{
                        fontSize: "22px",
                        lineHeight: "1.2",
                        fontFamily: "Montserrat Bold",
                      }}
                    >
                      ETH
                    </Typography>

                    <Typography
                      sx={{
                        fontSize: "18px",
                        lineHeight: "1.2",
                        fontFamily: "Montserrat",
                        color: "rgba(145, 153, 167, 1)",
                      }}
                    >
                      ${collateralTokenPriceList[1].toFixed(2)}
                    </Typography>
                  </Box>
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {balanceEth} ETH
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {collateralBalanceList[1].toFixed(3)}
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {collateralFreeCollateralList[1].toFixed(3)}
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  <Box sx={{ display: "flex", justifyContent: "flex-end" }}>
                    <Button
                      disabled={!connected}
                      sx={{
                        padding: "6px 16px",
                        borderRadius: "6px",
                        color: "rgb(238, 233, 226)",
                        textTransform: "none",
                        backgroundColor: "transparent",
                        boxSizing: "border-box",
                        border: "1px solid rgb(238, 233, 226)",
                        fontFamily: "Montserrat SemiBold",
                        "&: hover": {
                          backgroundColor: "rgb(234, 216, 184)",
                          color: "rgb(44, 46, 55)",
                        },
                        "&: disabled": {
                          backgroundColor: "transparent",
                          color: "rgb(238, 233, 226)",
                        },
                      }}
                      onClick={() => handleWithdrawModalOpen(5)}
                    >
                      Withdraw
                    </Button>
                    <Button
                      disabled={!connected}
                      sx={{
                        padding: "6px 16px",
                        borderRadius: "6px",
                        ml: 1,
                        color: "rgb(44, 46, 55)",
                        textTransform: "none",
                        backgroundColor: "rgb(248, 204, 130)",
                        fontFamily: "Montserrat SemiBold",
                        "&: hover": {
                          backgroundColor: "rgb(234, 216, 184)",
                          color: "rgb(44, 46, 55)",
                        },
                        "&: disabled": {
                          backgroundColor: "rgb(248, 204, 130)",
                          color: "rgb(44, 46, 55)",
                        },
                      }}
                      onClick={() => handleDepositModalOpen(5)}
                    >
                      Deposit
                    </Button>
                  </Box>
                </TableCell>
              </TableRow>
              <TableRow>
                <TableCell
                  component="th"
                  scope="row"
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                >
                  <Box>
                    <Typography
                      sx={{
                        fontSize: "22px",
                        lineHeight: "1.2",
                        fontFamily: "Montserrat Bold",
                      }}
                    >
                      WETH
                    </Typography>
                    <Typography
                      sx={{
                        fontSize: "18px",
                        lineHeight: "1.2",
                        fontFamily: "Montserrat",
                        color: "rgba(145, 153, 167, 1)",
                      }}
                    >
                      ${collateralTokenPriceList[1].toFixed(2)}
                    </Typography>
                  </Box>
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {balanceList[1].toFixed(3)} WETH
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {collateralBalanceList[1].toFixed(3)}
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {collateralFreeCollateralList[1].toFixed(3)}
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  <Box sx={{ display: "flex", justifyContent: "flex-end" }}>
                    <Button
                      disabled={!connected}
                      sx={{
                        padding: "6px 16px",
                        borderRadius: "6px",
                        color: "rgb(238, 233, 226)",
                        textTransform: "none",
                        backgroundColor: "transparent",
                        boxSizing: "border-box",
                        border: "1px solid rgb(238, 233, 226)",
                        fontFamily: "Montserrat SemiBold",
                        "&: hover": {
                          backgroundColor: "rgb(234, 216, 184)",
                          color: "rgb(44, 46, 55)",
                        },
                        "&: disabled": {
                          backgroundColor: "transparent",
                          color: "rgb(238, 233, 226)",
                        },
                      }}
                      onClick={() => handleWithdrawModalOpen(1)}
                    >
                      Withdraw
                    </Button>
                    <Button
                      disabled={!connected}
                      sx={{
                        padding: "6px 16px",
                        borderRadius: "6px",
                        ml: 1,
                        color: "rgb(44, 46, 55)",
                        textTransform: "none",
                        backgroundColor: "rgb(248, 204, 130)",
                        fontFamily: "Montserrat SemiBold",
                        "&: hover": {
                          backgroundColor: "rgb(234, 216, 184)",
                          color: "rgb(44, 46, 55)",
                        },
                        "&: disabled": {
                          backgroundColor: "rgb(248, 204, 130)",
                          color: "rgb(44, 46, 55)",
                        },
                      }}
                      onClick={() => handleDepositModalOpen(1)}
                    >
                      Deposit
                    </Button>
                  </Box>
                </TableCell>
              </TableRow>
              <TableRow>
                <TableCell
                  component="th"
                  scope="row"
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                >
                  <Box>
                    <Typography
                      sx={{
                        fontSize: "22px",
                        lineHeight: "1.2",
                        fontFamily: "Montserrat Bold",
                      }}
                    >
                      FRAX
                    </Typography>
                    <Typography
                      sx={{
                        fontSize: "18px",
                        lineHeight: "1.2",
                        fontFamily: "Montserrat",
                        color: "rgba(145, 153, 167, 1)",
                      }}
                    >
                      ${collateralTokenPriceList[2].toFixed(2)}
                    </Typography>
                  </Box>
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {balanceList[2].toFixed(2)} FRAX
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {collateralBalanceList[2].toFixed(2)}
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {collateralFreeCollateralList[2].toFixed(2)}
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  <Box sx={{ display: "flex", justifyContent: "flex-end" }}>
                    <Button
                      disabled={!connected}
                      sx={{
                        padding: "6px 16px",
                        borderRadius: "6px",
                        color: "rgb(238, 233, 226)",
                        textTransform: "none",
                        backgroundColor: "transparent",
                        boxSizing: "border-box",
                        border: "1px solid rgb(238, 233, 226)",
                        fontFamily: "Montserrat SemiBold",
                        "&: hover": {
                          backgroundColor: "rgb(234, 216, 184)",
                          color: "rgb(44, 46, 55)",
                        },
                        "&: disabled": {
                          backgroundColor: "transparent",
                          color: "rgb(238, 233, 226)",
                        },
                      }}
                      onClick={() => handleWithdrawModalOpen(2)}
                    >
                      Withdraw
                    </Button>
                    <Button
                      disabled={!connected}
                      sx={{
                        padding: "6px 16px",
                        borderRadius: "6px",
                        ml: 1,
                        color: "rgb(44, 46, 55)",
                        textTransform: "none",
                        backgroundColor: "rgb(248, 204, 130)",
                        fontFamily: "Montserrat SemiBold",
                        "&: hover": {
                          backgroundColor: "rgb(234, 216, 184)",
                          color: "rgb(44, 46, 55)",
                        },
                        "&: disabled": {
                          backgroundColor: "rgb(248, 204, 130)",
                          color: "rgb(44, 46, 55)",
                        },
                      }}
                      onClick={() => handleDepositModalOpen(2)}
                    >
                      Deposit
                    </Button>
                  </Box>
                </TableCell>
              </TableRow>
              <TableRow>
                <TableCell
                  component="th"
                  scope="row"
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                >
                  <Box>
                    <Typography
                      sx={{
                        fontSize: "22px",
                        lineHeight: "1.2",
                        fontFamily: "Montserrat Bold",
                      }}
                    >
                      OP
                    </Typography>
                    <Typography
                      sx={{
                        fontSize: "18px",
                        lineHeight: "1.2",
                        fontFamily: "Montserrat",
                        color: "rgba(145, 153, 167, 1)",
                      }}
                    >
                      ${collateralTokenPriceList[3].toFixed(2)}
                    </Typography>
                  </Box>
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {balanceList[3].toFixed(2)} OP
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {collateralBalanceList[3].toFixed(2)}
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {collateralFreeCollateralList[3].toFixed(2)}
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  <Box sx={{ display: "flex", justifyContent: "flex-end" }}>
                    <Button
                      disabled={!connected}
                      sx={{
                        padding: "6px 16px",
                        borderRadius: "6px",
                        color: "rgb(238, 233, 226)",
                        textTransform: "none",
                        backgroundColor: "transparent",
                        boxSizing: "border-box",
                        border: "1px solid rgb(238, 233, 226)",
                        fontFamily: "Montserrat SemiBold",
                        "&: hover": {
                          backgroundColor: "rgb(234, 216, 184)",
                          color: "rgb(44, 46, 55)",
                        },
                        "&: disabled": {
                          backgroundColor: "transparent",
                          color: "rgb(238, 233, 226)",
                        },
                      }}
                      onClick={() => handleWithdrawModalOpen(3)}
                    >
                      Withdraw
                    </Button>
                    <Button
                      disabled={!connected}
                      sx={{
                        padding: "6px 16px",
                        borderRadius: "6px",
                        ml: 1,
                        color: "rgb(44, 46, 55)",
                        textTransform: "none",
                        backgroundColor: "rgb(248, 204, 130)",
                        fontFamily: "Montserrat SemiBold",
                        "&: hover": {
                          backgroundColor: "rgb(234, 216, 184)",
                          color: "rgb(44, 46, 55)",
                        },
                        "&: disabled": {
                          backgroundColor: "rgb(248, 204, 130)",
                          color: "rgb(44, 46, 55)",
                        },
                      }}
                      onClick={() => handleDepositModalOpen(3)}
                    >
                      Deposit
                    </Button>
                  </Box>
                </TableCell>
              </TableRow>
              <TableRow
                sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
              >
                <TableCell
                  component="th"
                  scope="row"
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                >
                  <Box>
                    <Typography
                      sx={{
                        fontSize: "22px",
                        lineHeight: "1.2",
                        fontFamily: "Montserrat Bold",
                      }}
                    >
                      USDT
                    </Typography>
                    <Typography
                      sx={{
                        fontSize: "18px",
                        lineHeight: "1.2",
                        fontFamily: "Montserrat",
                        color: "rgba(145, 153, 167, 1)",
                      }}
                    >
                      ${collateralTokenPriceList[4].toFixed(2)}
                    </Typography>
                  </Box>
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {balanceList[4].toFixed(2)} USDT
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {collateralBalanceList[4].toFixed(2)}
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  {collateralFreeCollateralList[4].toFixed(2)}
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "22px",
                    fontFamily: "Montserrat Bold",
                    borderBottom: "1px solid rgba(67, 70, 74, 1)",
                    color: "white",
                  }}
                  align="right"
                >
                  <Box sx={{ display: "flex", justifyContent: "flex-end" }}>
                    <Button
                      disabled={!connected}
                      sx={{
                        padding: "6px 16px",
                        borderRadius: "6px",
                        color: "rgb(238, 233, 226)",
                        textTransform: "none",
                        backgroundColor: "transparent",
                        boxSizing: "border-box",
                        border: "1px solid rgb(238, 233, 226)",
                        fontFamily: "Montserrat SemiBold",
                        "&: hover": {
                          backgroundColor: "rgb(234, 216, 184)",
                          color: "rgb(44, 46, 55)",
                        },
                        "&: disabled": {
                          backgroundColor: "transparent",
                          color: "rgb(238, 233, 226)",
                        },
                      }}
                      onClick={() => handleWithdrawModalOpen(4)}
                    >
                      Withdraw
                    </Button>
                    <Button
                      disabled={!connected}
                      sx={{
                        padding: "6px 16px",
                        borderRadius: "6px",
                        ml: 1,
                        color: "rgb(44, 46, 55)",
                        textTransform: "none",
                        backgroundColor: "rgb(248, 204, 130)",
                        fontFamily: "Montserrat SemiBold",
                        "&: hover": {
                          backgroundColor: "rgb(234, 216, 184)",
                          color: "rgb(44, 46, 55)",
                        },
                        "&: disabled": {
                          backgroundColor: "rgb(248, 204, 130)",
                          color: "rgb(44, 46, 55)",
                        },
                      }}
                      onClick={() => handleDepositModalOpen(4)}
                    >
                      Deposit
                    </Button>
                  </Box>
                </TableCell>
              </TableRow>
            </TableBody>
          )}
        </Table>
      </TableContainer>
      <Modal
        open={openDepositModal}
        onClose={handleModalClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={modalStyle}>
          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            <Typography
              sx={{
                fontSize: "18px",
                fontFamily: "Montserrat Bold",
                color: "white",
              }}
            >
              Deposit
            </Typography>
            <IconButton sx={{ padding: "0" }} onClick={handleModalClose}>
              <CloseIcon sx={{ color: "white" }} />
            </IconButton>
          </Box>
          <Box sx={{ display: "flex", justifyContent: "space-between", mt: 3 }}>
            <Button
              sx={{
                border: "1px solid rgba(255, 255, 255, 0.12)",
                borderRadius: "10px",
                padding: "4px 24px",
                color: "white",
                fontSize: "18px",
                fontFamily: "Montserrat Medium",
                mr: 1,
              }}
            >
              {TokenList[modalIndex]}
            </Button>
            <FormControl sx={{ flexGrow: "1" }} variant="outlined">
              <OutlinedInput
                id="outlined-adornment-weight"
                value={tokenValue}
                onChange={handleTokenValueChange}
                aria-describedby="outlined-weight-helper-text"
                endAdornment={
                  <InputAdornment position="end">
                    <Button
                      sx={{
                        color: "white",
                        border: "1px solid rgba(255, 255, 255, 0.12)",
                        borderRadius: "12px",
                        padding: "2px 4px",
                        minWidth: "44px",
                        fontSize: "10px",
                      }}
                      onClick={handleMaxValue}
                    >
                      Max
                    </Button>
                  </InputAdornment>
                }
                sx={{
                  borderRadius: "8px",
                  color: "white",
                  pr: 1,
                  border: "1px solid rgba(255, 255, 255, 0.12)",
                  "& .MuiOutlinedInput-input": { padding: "12px 8px" },
                  "& .MuiOutlinedInput-notchedOutline": { border: "none" },
                }}
              />
            </FormControl>
          </Box>
          <Box sx={{ display: "flex", justifyContent: "flex-start", mt: 2 }}>
            <Typography
              sx={{
                fontFamily: "Montserrat Medium",
                mr: 3,
                fontSize: "12px",
                color: "rgba(145, 153, 167, 1)",
              }}
            >
              Balance:
            </Typography>
            {isUserAccountCalculated && modalIndex <= 4 && (
              <Typography
                sx={{
                  fontFamily: "Montserrat Medium",
                  fontSize: "12px",
                  color: "white",
                }}
              >
                {balanceList[modalIndex].toFixed(2)}
              </Typography>
            )}
            {isUserAccountCalculated && modalIndex === 5 && (
              <Typography
                sx={{
                  fontFamily: "Montserrat Medium",
                  fontSize: "12px",
                  color: "white",
                }}
              >
                {balanceEth}
              </Typography>
            )}
          </Box>
          {modalIndex === 5 ? (
            <Button
              sx={{
                borderRadius: "8px",
                width: "100%",
                border: "1px solid rgba(255, 255, 255, 0.12)",
                fontFamily: "Montserrat SemiBold",
                background: "rgb(37, 37, 37)",
                padding: 0,
                height: "50px",
                color: "rgb(255, 184, 0)",
                mt: 1,
                "& :hover": { background: "rgb(43, 42, 42)" },
              }}
              onClick={() => {
                onSeekDeposit();
              }}
            >
              Deposit
            </Button>
          ) : isUserAccountCalculated &&
            allowanceList[modalIndex].toNumber() > 0 ? (
            <Button
              sx={{
                borderRadius: "8px",
                width: "100%",
                border: "1px solid rgba(255, 255, 255, 0.12)",
                fontFamily: "Montserrat SemiBold",
                background: "rgb(37, 37, 37)",
                padding: 0,
                height: "50px",
                color: "rgb(255, 184, 0)",
                mt: 1,
                "& :hover": { background: "rgb(43, 42, 42)" },
              }}
              onClick={() => {
                onSeekDeposit();
              }}
            >
              Deposit
            </Button>
          ) : (
            <Button
              sx={{
                borderRadius: "8px",
                width: "100%",
                border: "1px solid rgba(255, 255, 255, 0.12)",
                fontFamily: "Montserrat SemiBold",
                background: "rgb(37, 37, 37)",
                padding: 0,
                height: "50px",
                color: "rgb(255, 184, 0)",
                mt: 1,
                "& :hover": { background: "rgb(43, 42, 42)" },
              }}
              onClick={() => {
                onSeekApproval();
              }}
            >
              Approve
            </Button>
          )}
        </Box>
      </Modal>
      <Modal
        open={openWithdrawModal}
        onClose={handleModalClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={modalStyle}>
          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            <Typography
              sx={{
                fontSize: "18px",
                fontFamily: "Montserrat Bold",
                color: "white",
              }}
            >
              Withdraw
            </Typography>
            <IconButton sx={{ padding: "0" }} onClick={handleModalClose}>
              <CloseIcon sx={{ color: "white" }} />
            </IconButton>
          </Box>
          <Box sx={{ display: "flex", justifyContent: "space-between", mt: 3 }}>
            <Button
              sx={{
                border: "1px solid rgba(255, 255, 255, 0.12)",
                borderRadius: "10px",
                padding: "4px 24px",
                color: "white",
                fontSize: "18px",
                fontFamily: "Montserrat Medium",
                mr: 1,
              }}
            >
              {TokenList[modalIndex]}
            </Button>
            <FormControl sx={{ flexGrow: "1" }} variant="outlined">
              <OutlinedInput
                id="outlined-adornment-weight"
                value={tokenValue}
                onChange={handleTokenValueChange}
                aria-describedby="outlined-weight-helper-text"
                endAdornment={
                  <InputAdornment position="end">
                    <Button
                      sx={{
                        color: "white",
                        border: "1px solid rgba(255, 255, 255, 0.12)",
                        borderRadius: "12px",
                        padding: "2px 4px",
                        minWidth: "44px",
                        fontSize: "10px",
                      }}
                      onClick={handleMaxValue}
                    >
                      Max
                    </Button>
                  </InputAdornment>
                }
                sx={{
                  borderRadius: "8px",
                  color: "white",
                  pr: 1,
                  border: "1px solid rgba(255, 255, 255, 0.12)",
                  "& .MuiOutlinedInput-input": { padding: "12px 8px" },
                  "& .MuiOutlinedInput-notchedOutline": { border: "none" },
                }}
              />
            </FormControl>
          </Box>
          <Box sx={{ display: "flex", justifyContent: "flex-start", mt: 2 }}>
            <Typography
              sx={{
                fontFamily: "Montserrat Medium",
                mr: 3,
                fontSize: "12px",
                color: "rgba(145, 153, 167, 1)",
              }}
            >
              Balance:
            </Typography>
            {isUserAccountCalculated && modalIndex <= 4 && (
              <Typography
                sx={{
                  fontFamily: "Montserrat Medium",
                  fontSize: "12px",
                  color: "white",
                }}
              >
                {collateralFreeCollateralList[modalIndex].toFixed(3)}
              </Typography>
            )}
            {isUserAccountCalculated && modalIndex === 5 && (
              <Typography
                sx={{
                  fontFamily: "Montserrat Medium",
                  fontSize: "12px",
                  color: "white",
                }}
              >
                {collateralFreeCollateralList[1].toFixed(3)}
              </Typography>
            )}
          </Box>
          <Button
            sx={{
              borderRadius: "8px",
              width: "100%",
              border: "1px solid rgba(255, 255, 255, 0.12)",
              fontFamily: "Montserrat SemiBold",
              background: "rgb(37, 37, 37)",
              padding: 0,
              height: "50px",
              color: "rgb(255, 184, 0)",
              mt: 1,
              "& :hover": { background: "rgb(43, 42, 42)" },
            }}
            onClick={() => {
              onSeekWithdraw();
            }}
          >
            Withdraw
          </Button>
        </Box>
      </Modal>
    </Box>
  );
}

export default CollateralBoard;
