import { createSlice,createAsyncThunk, createSelector } from "@reduxjs/toolkit";
import { JsonRpcProvider, StaticJsonRpcProvider } from "@ethersproject/providers";
import { ethers } from "ethers";
import { Networks, DEFAULD_NETWORK } from "../../constants/blockchain";
import { warning, success, info, error } from "./messages-slice";
import { messages } from "../../constants/messages";
import { setAll } from "../../helpers/set-all";
import { PerpetualProtocol, PositionSide, PositionDraft } from "@perp/sdk-curie";
import { metamaskErrorWrap } from "../../helpers/metamask-error-wrap";
import { sleep } from "../../helpers/sleep";
import Big from "big.js";
import { RootState } from "../store";

interface ICalcUserCollateralDetails { 
	address: string;
	networkID: Networks;
	perpetualObject: PerpetualProtocol;		
}

export const calcUserCollateralDetails = createAsyncThunk("user/calcUserCollateralDetails", 
	async({ address, networkID, perpetualObject }: ICalcUserCollateralDetails) => {
		if( networkID === DEFAULD_NETWORK ) {  
			await perpetualObject.init();
			const _freeCollateral = await perpetualObject.contractReader.getFreeCollateral(address); 
			const _accountValue = await perpetualObject.contractReader.getAccountValue(address); 
			const _collateralBalanceList = await perpetualObject.vault!.getBalanceList();				
			const _collateralFreeCollateralList = await perpetualObject.vault!.getFreeCollateralList();		
			console.log("Collateral Balance List", _collateralBalanceList);			
			console.log("Collateral Free Collateral List", _collateralFreeCollateralList);
			return {
				freeCollateral: _freeCollateral.toFixed(4),
				accountValue: _accountValue.toFixed(4),
				collateralBalanceList: _collateralBalanceList,
				collateralFreeCollateralList: _collateralFreeCollateralList,
			}
		}
});

interface ICalcUserAccountDetails {
	networkID: Networks;
  provider: StaticJsonRpcProvider | JsonRpcProvider;
	perpetualObject: PerpetualProtocol;		
}

export const calcUserAccountDetails = createAsyncThunk("user/calcUserAccountlDetails", 
	async({ networkID, provider, perpetualObject }: ICalcUserAccountDetails) => {
		if( networkID === DEFAULD_NETWORK ) {  
			await perpetualObject.init();
      await perpetualObject.connect({ signer: provider.getSigner() });			
      const _allowanceList = await perpetualObject.wallet!.getAllowanceList();      
      const _balanceList = await perpetualObject.wallet!.getBalanceList(); 
      const _balanceEth = await perpetualObject.wallet!.getBalanceEth();    
      const _collateraltokenpricelist = await perpetualObject.wallet!.getCollateralTokenPriceList();  
      const _collateralTokenList = perpetualObject.wallet!.collateralTokenList; 
      console.log("Collateral Token List", _collateralTokenList);  
			
			console.log("Allowance List", _allowanceList);
			return {		
        allowanceList: _allowanceList,			
        balanceList: _balanceList,
        balanceEth: _balanceEth.toFixed(3),
        collateralTokenPriceList: _collateraltokenpricelist,        
			}
		}
});

export interface IChangeApproval {
	provider: StaticJsonRpcProvider | JsonRpcProvider;	
	networkID: Networks;	
	tokenIndex: number;
	perpetualObject: PerpetualProtocol;
}

export const changeApproval = createAsyncThunk("user/changeApproval", async ({ provider, networkID, tokenIndex, perpetualObject }: IChangeApproval, { dispatch }) => {
	if (!provider) {
		dispatch(warning({ text: messages.please_connect_wallet }));
		return;
	}

	if( networkID === DEFAULD_NETWORK ) {
		await perpetualObject.init();
    await perpetualObject.connect({ signer: provider.getSigner() });

		let approveTx;
		try {					
			const collateralTokenList = perpetualObject.wallet!.collateralTokenList;			
			approveTx = await perpetualObject.wallet!.approve(collateralTokenList[tokenIndex]);				
			await approveTx.transaction.wait();
			dispatch(success({ text: messages.tx_successfully_send }));
		} catch (err: any) {			
			return metamaskErrorWrap(err, dispatch);
		} finally {				
		}
		await sleep(4);
		await dispatch(calcUserAccountDetails({networkID, provider, perpetualObject}));	
		dispatch(info({ text: messages.your_allowance_updated }));
		return;
	}	
});

export interface IChangeCollateral {
	address: string;
	provider: StaticJsonRpcProvider | JsonRpcProvider;	
	networkID: Networks;	
	tokenIndex?: number;
	amount: Big;
	perpetualObject: PerpetualProtocol;
}

export const changeDepositCollateral = createAsyncThunk("user/changeDepositCollateral", async ({address, provider, networkID, tokenIndex, amount, perpetualObject }: IChangeCollateral, { dispatch }) => {
	if (!provider) {
		dispatch(warning({ text: messages.please_connect_wallet }));
		return;
	}

	if( networkID === DEFAULD_NETWORK ) {
		await perpetualObject.init();
    await perpetualObject.connect({ signer: provider.getSigner() });

		let depositTx;
		try {			
			if(tokenIndex !== undefined) {
				const collateralTokenList = perpetualObject.wallet!.collateralTokenList;			
				depositTx = await perpetualObject.vault!.deposit(collateralTokenList[tokenIndex], amount);
				await depositTx.transaction.wait();
			}	else {
				depositTx = await perpetualObject.vault!.depositETH(amount);
				await depositTx.transaction.wait();
			}				
			dispatch(success({ text: messages.tx_successfully_send }));
		} catch (err: any) {
			console.log("Error", err);
				return metamaskErrorWrap(err, dispatch);
		} finally {				
		}
		await sleep(4);
		await dispatch(calcUserAccountDetails({networkID, provider, perpetualObject}));	
		await dispatch(calcUserCollateralDetails({address, networkID, perpetualObject}));	
		dispatch(info({ text: messages.your_deposit_updated }));
		return;
	}	
});

